import service from '../../service.js';
export default {
  data() {
    return {
      indicatorDots: true,
      autoplay: true,
      interval: 2000,
      duration: 500,
      indicatorActiveColor: '#ffffff',
      searchInfo: '大家都在搜“森海塞尔”',
      swiperList: [{
        imgUrl:"https://market.pd-unixe.com/uploads/201906111745582db721897.png"
      }, {
        imgUrl:"https://market.pd-unixe.com/uploads/2019061117455884a819697.jpg"
      }, {
        imgUrl:"https://market.pd-unixe.com/uploads/20190611174558d5c576479.png"
      }, {
        imgUrl:"https://market.pd-unixe.com/uploads/201906111745539eac11543.png"
      }],
      tabList: [],
      selectIndex: 0,
      cultureList: []
    }
  },
  watch: {
    selectIndex(val) {
      this.getDefault(this.tabList[val].category_id)
    }
  },
  onLoad() {
    this.getCategorylist()
    this.getDefault()
    this.getBanner()
  },
  methods: {
    // 获取文章
    getDefault(id) {
      this.$http({
        url: this.$api.articlesbycategoryid,
        data: {
          'category_id': id? id: ''
        },
        cb: (err, res) => {
          if (!err && res.code === 1) {
            this.cultureList = res.data.list
            if (res.data.list.length === 0) {
              uni.showToast({
              	title: '当前分类文章为空',
                icon: 'none'
              })
            }
            return
          } else {
            uni.showToast({
            	title: '文章列表获取失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 获取文章分类
    getCategorylist() {
      this.$http({
        data: {
          'wxapp_id': 10001,
          token: 'b612f5e2a32d553fdaea8eeb06bc2744',  
        },
        url: this.$api.categorylist,
        cb: (err, res) => {
          if (!err && res.code === 1) { 
            this.tabList = res.data.categoryList
            return
          } else {
            uni.showToast({
            	title: '文章分类获取失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 点赞
    zanAction(item, index) {
      console.log(item.article_id, item.islike, index)
      let url = this.$api.unLike
      if(item.islike === 'no') {
        url = this.$api.like
      }
      this.$http({
        url: url,
        data: {
          article_id: item.article_id
        },
        cb: (err, res) => {
          if(!err && res) {
            switch(this.cultureList[index].islike) {
              case 'yes':
                uni.showToast({
                  title: '取消点赞成功',
                  icon: 'none'
                })
                this.cultureList[index].islike = 'no'
                this.cultureList[index].like_count -= 1
                break
              case 'no':
                uni.showToast({
                  title: '点赞成功',
                  icon: 'none'
                })
                this.cultureList[index].islike = 'yes'
                this.cultureList[index].like_count += 1
                break
            }
          } else {
            switch(this.cultureList[index].islike) {
              case 'yes':
                uni.showToast({
                	title: '取消点赞失败',
                  icon: 'none'
                })
                break
              case 'no':
                uni.showToast({
                	title: '点赞失败请重试',
                  icon: 'none'
                })
                break
            }
          }
        }
      })
    },
    // 首页轮播图图片
    getBanner() {
      this.$http({
        url: this.$api.index,
        cb: (err, res) => {
          if(!err && res.code === 1) {
            // console.log(res.data.items[0].data)
            
            // 替换轮播图图片路径数据
            // this.swiperList = res.data.items[0].data
          } else {
            uni.showToast({
            	title: '轮播图图片加载失败',
              icon: 'none'
            })
          }
          // console.log(res.data.items[0].data[0].imgUrl)
        }
      })
    },
    // 选择分类
    selectTab(item, index) {
      this.selectIndex = index
    }, 
    // 文章详情页
    goInfo(item) {
      uni.navigateTo({
        url: '../components/shareInfo?article_id=' + item
      })
    },
    // 搜索页
    goSearch() {
      uni.navigateTo({
        url: '../components/search?type=0'
      })
    }
  }
}
